*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Regular.f670c6ce.woff2");
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Medium.e140c6d1.woff2");
  font-weight: 500;
}

@font-face {
  font-family: DM Sans;
  src: url("DMSans-Bold.17fd81d5.woff2");
  font-weight: 700;
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: #111;
  background-color: #f6f6f6;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  transition: background-color .3s ease-in-out;
}

section {
  width: 100%;
}

ul {
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  color: inherit;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input {
  font-family: inherit;
}

address {
  font-style: normal;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.overflow-hide {
  overflow: hidden;
}

.sing-up-btn {
  color: #fff;
  max-width: 184px;
  background-color: #4f2ee8;
  align-items: center;
  gap: 90px;
  padding: 14px 15px;
  display: flex;
}

.sing-up-btn:hover {
  color: #111;
  background-color: #fff;
}

.log-out-btn {
  max-width: 118px;
  background-color: #fff;
  align-items: center;
  gap: 4px;
  margin-top: auto;
  padding: 15px 24px;
  display: none;
}

.log-out-btn:hover {
  color: #fff;
  background-color: #4f2ee8;
}

.style-btn {
  letter-spacing: -.04em;
  white-space: nowrap;
  border: 1.5px solid #111;
  border-radius: 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  transition: all .3s ease-in-out;
}

.menu-link {
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 500;
  transition: all .3s ease-in-out;
}

.menu-link:hover, .menu-link:active {
  color: #111;
  background-color: #eac645;
}

.current-link {
  color: #111;
  background-color: #eac645;
  font-weight: 700;
}

.current-link .menu-icon-shop {
  fill: #111;
}

.home-link {
  max-width: 72px;
  display: block;
}

.shopping-link {
  align-items: center;
  gap: 4px;
  margin-left: -16px;
  display: flex;
}

.shopping-link:hover .menu-icon-shop {
  fill: #111;
}

.menu-icon-shop {
  fill: #111;
  transition: all .3s ease-in-out;
}

.menu-icon-arrow-right {
  stroke: #eac645;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1440px) {
  main {
    max-width: 1440px;
    gap: 40px;
    margin: 0 auto;
    padding: 40px 24px;
    display: flex;
  }

  .container {
    max-width: 996px;
    padding: 0;
  }
}

.header {
  height: 64px;
  max-width: 1440px;
  z-index: 998;
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  margin: 0 auto;
  transition: background-color .3s ease-in-out;
  position: relative;
}

.header .container {
  height: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__main-logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header__menu-list {
  letter-spacing: -.04em;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  gap: 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: none;
}

.header__right {
  align-items: center;
  gap: 14px;
  display: flex;
}

.header__switcher {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 18px;
  padding: 1px;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.header__switcher:before {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
}

.header .animation:before {
  transition: all .3s ease-in-out;
}

.header__checkbox:checked + .header__switcher {
  background-image: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.header__checkbox:checked + .header__switcher:before {
  transform: translateX(20px);
}

.header .sing-up-btn {
  display: none;
}

.header__mobile-menu-btn {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
}

.header__mobile-menu-btn:hover .header__mobile-menu-btn__icon {
  stroke: #4f2ee8;
}

.header__mobile-menu-btn__icon {
  stroke: #111;
  transition: all .3s ease-in-out;
}

.header__scroll-btn {
  width: 54px;
  height: 54px;
  opacity: .9;
  background-color: #4f2ee8;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  display: none;
  position: fixed;
  bottom: 50px;
  right: 25px;
  box-shadow: 0 0 0 5px rgba(79, 46, 232, .3);
}

.header__scroll-btn__icon {
  fill: #fff;
}

.header__scroll-btn:hover {
  opacity: 1;
  transform: scale(1.1);
}

@media (min-width: 768px) {
  .header {
    height: 72px;
  }

  .header__left {
    gap: 40px;
    display: flex;
  }

  .header__mobile-menu-btn {
    display: none;
  }

  .header__menu-list, .header .sing-up-btn {
    display: flex;
  }

  .header__right {
    gap: 24px;
    display: flex;
  }
}

@media (min-width: 1440px) {
  .header .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.aside {
  max-width: 375px;
  margin: 0 auto 40px;
  padding: 40px 20px 0;
}

.category {
