}

.support__list {
  max-height: 292px;
  scroll-behavior: smooth;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
  overflow: hidden;
}

.support__item {
  align-items: center;
  gap: 16px;
  display: flex;
}

.support__index {
  color: #fff;
  letter-spacing: -.04em;
  font-size: 14px;
  line-height: 1.29;
}

.support__link {
  height: 32px;
  align-items: center;
  display: flex;
}

.support__image {
  filter: brightness(0) invert();
